// Styles for all media types

// Webfonts

// see http://compass-style.org/reference/compass/css3/font_face/
// @include font-face($name, $font-files, $eot, $weight, $style);

// Headings

h1 {
  font: $fontstyle-default $fontweight-secondary #{$fontsize-biggest}/#{$lineheight-default}
    $fontfamily-secondary;
}
h2 {
  font: $fontstyle-default $fontweight-secondary #{$fontsize-bigger}/#{$lineheight-default}
    $fontfamily-secondary;
}
h3 {
  font: $fontstyle-default $fontweight-secondary #{$fontsize-big}/#{$lineheight-default}
    $fontfamily-secondary;
}
h4 {
  font: $fontstyle-default $fontweight-default-bold #{$fontsize-default}/#{$lineheight-default}
    $fontfamily-default;
}

// Text

a {
